<template>
  <v-container app class="container c_box">
    <div class="sign_in_box">
      <div class="sign_in_top" :class="isApp ? 'sign_in_top_app' : 'sign_in_top_pc'">
        <div class="sign_in_top_img">
          <img :src="loadIcon(`day_title_${languageType}`, false)" />
        </div>
      </div>
      <div class="sign_in_chest" :class="isApp ? 'sign_in_chest_app' : 'sign_in_chest_pc'">
        <div class="chest_box">
          <div class="chest_box_item" v-for="item in list" :key="item.day_num" @click="openChest(item)">
            <img class="chest_box_item_img" :src="loadIcon(mapIcon[item.reward_type], false)" alt="" />
            <div class="chest_box_item_gold">
              <img class="chest_box_item_gold_img" :src="loadIcon('day_gold', false)" alt="" />
              <span class="chest_box_item_gold_title">{{ `${currencySymbol}${item.gold}` }}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="sign_in_btn" :class="isApp ? 'sign_in_btn_app' : 'sign_in_btn_pc'">
        <v-btn class="btn_radius sign_in_btn_box" @click="onRecharge" color="t_primary_dark_color" height="36" block>
          <div>{{ $t('UI.MakeMoney_4_Promotion_17') }}</div>
        </v-btn>
      </div>
      <div class="sign_in_text" :class="isApp ? 'sign_in_text_app' : 'sign_in_text_pc'">
        <div class="text_box" v-html="contentText"></div>
      </div>
    </div>
  </v-container>
</template>

<script>
import { mapState } from 'vuex'
import { getEeplace } from '@/utils/helper'
import { getFirstRechargeSignList, receiveReward } from '@/api/modules/agent'
import loadIcon from '@/assets/icons'
import PublishSubscribe from 'publish-subscribe-js'

export default {
  data() {
    return {
      mapIcon: {
        1: 'box_linqu', // 已领取
        2: 'box_active', // 可领取
        3: 'box_lock', // 未解锁
        4: 'box_fail' // 已解锁不可领取
      },
      list: [],
      content: ''
    }
  },
  computed: {
    ...mapState('switchState', ['currencyShow']),
    ...mapState('base', ['isApp', 'systemConfig', 'languageType', 'user', 'showRedDot']),
    // 货币标识
    currencySymbol() {
      const symbol = this.systemConfig && this.systemConfig.currency_symbol ? this.systemConfig.currency_symbol : ''
      return this.currencyShow ? symbol : ''
    },
    // 处理富文本
    contentText() {
      return this.content ? getEeplace(this.content) : ''
    },
    // 七日签到红点
    signinRed() {
      const { first_recharge_sign_dot = {} } = this.showRedDot
      const { is_show = 0 } = first_recharge_sign_dot

      return !!Number(is_show)
    }
  },
  watch: {
    // 监听红点，刷新列表
    signinRed() {
      this.getList()
    }
  },
  created() {
    this.getList()
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    // 签到列表
    async getList() {
      const params = {
        token: this.user.token,
        language: this.languageType
      }

      const res = await getFirstRechargeSignList(params).catch(e => e)
      if (!res || res.code != 0) return

      const resData = res && res.data && Object.prototype.toString.call(res.data) == '[object Object]' ? res.data : {}
      const { sign_data = [], content = '' } = resData
      this.list = sign_data
      this.content = content
    },
    // 领取金币
    async openChest(item = {}) {
      const { reward_type = 4, reward_index = 1, day_num = 1 } = item
      if (reward_type != 2) return

      const params = {
        token: this.user.token,
        language: this.languageType,
        reward_index,
        day_num
      }
      const res = await receiveReward(params).catch(e => e)
      if (!res || res.code != 0) return

      // 手动更新宝箱状态
      item.reward_type = 1
      const resData = res && res.data && Object.prototype.toString.call(res.data) == '[object Object]' ? res.data : {}
      // 更新红点
      this.$store.dispatch('base/showRedDotReceivedFun', {
        language: this.languageType,
        token: this.user.token,
        version: 2,
        event: 'first_recharge_sign_dot'
      })
      // 提现成功弹窗 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: this.$t('UI_Code_Bonus'),
          value: resData.reward_gold || 0
        }
      })
    },
    // 充值
    onRecharge() {
      this.$store.dispatch('modal/setDepositWithdrawShow')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/activity.modules.scss';

.container {
  padding: 0;
}

.sign_in_box {
  padding: $sign_in_box_padding;

  .sign_in_top_app {
    background: $sign_in_top_app_bg;
    width: $sign_in_top_app_width;
    height: $sign_in_top_app_height;

    .sign_in_top_img {
      width: $sign_in_top_img_app_width;
      @include setBox(row, center, center);

      img {
        margin: $sign_in_top_img_app_img_margin;
        height: $sign_in_top_img_app_img_height;
      }
    }
  }

  .sign_in_top_pc {
    background: $sign_in_top_pc_bg;
    width: $sign_in_top_pc_width;
    height: $sign_in_top_pc_height;

    .sign_in_top_img {
      width: $sign_in_top_img_pc_width;
      @include setBox(row, center, center);

      img {
        margin: $sign_in_top_img_pc_img_margin;
        height: $sign_in_top_img_pc_img_height;
        width: $sign_in_top_img_pc_img_width;
      }
    }
  }

  .sign_in_chest {
    .chest_box {
      @include setBox(row, center, center, wrap);

      .chest_box_item {
        @include setBox(column, center, center);
      }
    }
  }

  .sign_in_chest_pc {
    margin: $sign_in_chest_pc_margin;
    width: $sign_in_chest_pc_width;

    .chest_box_item_img {
      width: $chest_box_item_img_pc_width;
      height: $chest_box_item_img_pc_height;
      cursor: pointer;
    }

    .chest_box_item_gold {
      height: $chest_box_item_gold_pc_height;
      background: $chest_box_item_gold_pc_bg;
      border-radius: $chest_box_item_gold_pc_border_radius;
      @include setBox(row, center, center);
      padding: $chest_box_item_gold_pc_padding;

      .chest_box_item_gold_img {
        width: $chest_box_item_gold_img_pc_width;
        height: $chest_box_item_gold_img_pc_height;
      }

      .chest_box_item_gold_title {
        font-size: $chest_box_item_gold_title_pc_font_size;
        font-weight: $chest_box_item_gold_title_pc_font_weight;
        color: $chest_box_item_gold_title_pc_color;
        margin: $chest_box_item_gold_title_pc_margin;
      }
    }
  }

  .sign_in_chest_app {
    margin: $sign_in_chest_app_margin;

    .chest_box_item_img {
      width: $chest_box_item_img_app_width;
      height: $chest_box_item_img_app_height;
      cursor: pointer;
    }

    .chest_box_item_gold {
      height: $chest_box_item_gold_app_height;
      background: $chest_box_item_gold_app_bg;
      border-radius: $chest_box_item_gold_app_border_radius;
      @include setBox(row, center, center);
      padding: $chest_box_item_gold_app_padding;

      .chest_box_item_gold_img {
        width: $chest_box_item_gold_img_app_width;
        height: $chest_box_item_gold_img_app_height;
      }

      .chest_box_item_gold_title {
        font-size: $chest_box_item_gold_title_app_font_size;
        font-weight: $chest_box_item_gold_title_app_font_weight;
        color: $chest_box_item_gold_title_app_color;
        margin: $chest_box_item_gold_title_app_margin;
      }
    }
  }

  .sign_in_text_app {
    padding: $sign_in_text_app_padding;

    .text_box {
      margin: $sign_in_text_app_text_box_margin;
      min-height: $sign_in_text_app_text_box_min_height;
      border: 1px solid $sign_in_text_app_text_box_border_color;
      opacity: 0.3;
      border-radius: $sign_in_text_app_text_box_border_radius;
      padding: $sign_in_text_app_text_box_padding;
    }
  }

  .sign_in_text_pc {
    width: $sign_in_text_pc_width;
    padding: $sign_in_text_pc_padding;
    margin: $sign_in_text_pc_margin;

    .text_box {
      margin: $sign_in_text_pc_text_box_margin;
      opacity: 0.3;
      padding: $sign_in_text_pc_text_box_padding;
    }
  }

  .sign_in_btn {
    @include setBox(row, center, center);
  }

  .sign_in_btn_app {
    width: $sign_in_btn_app_width;
    margin: $sign_in_btn_app_margin;
  }

  .sign_in_btn_pc {
    width: $sign_in_btn_pc_width;
    margin: $sign_in_btn_pc_margin;
  }
}

@media (min-width: 1080px) {
  .sign_in_box {
    width: $sign_in_box_pc_width;
  }
}
</style>
